html {
  text-transform: uppercase;
  --main-color: #000;
  --link-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  display: inline-block;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-align: center;
  color: var(--main-color);
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.logo__img {
  vertical-align: top;
  width: 40px;
  height: 40px;
}

.nav__link:hover, .hover {
