}

.is-active:after {
  color: var(--link-color);
  background-color: var(--link-color);
  content: "";
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: var(--link-color);
}
